import React, {Component} from "react";
import {Table, Button, Divider, Avatar, message, List, Badge, Pagination, ConfigProvider, Tag } from 'antd';
import {inject, observer} from 'mobx-react';
import * as  mobx from 'mobx';
import {showText} from "@/utils/utils";
import moment from 'moment';
import DetailModal from '@/components/digitalOrg/DetailModal';
import DetailOfficeModal from '@/components/digitalOrg/DetailOfficeModal';
import emitter from "@/utils/events";

const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '20px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);
let officeArray = ["weekreport"];  //  

@inject('DigtalOrgStore')
@inject('OfficeManageStore')
@observer
export default class Notify extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.DigtalOrgStore;
		this.officeStore = this.props.OfficeManageStore;
		this.state = {
			selectedRowKeys: [],
			selectedID: "",
			visible: false,
			visibleOffic: false,
			mainType: "", // 
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
		this.store.notifyParams = Object.assign({}, {
			type: 2,
			pageIndex: 1,
			pageSize: 10
		});
		this.store.getNotifyPageList();
		this.store.getJournalTypeList();
		this.store.getNewNotifyCount();
	}

	onSelectChange = selectedRowKeys => {
		this.setState({selectedRowKeys});
	};

	showSizeChange = async (current, pageSize) => {
		this.store.notifyParams.pageIndex = 1;
		this.store.notifyParams.pageSize = pageSize;
		this.store.getNotifyPageList();
	}

	onPageChange = async (pageNumber) => {
		this.store.notifyParams.pageIndex = pageNumber;
		this.store.getNotifyPageList();
	}

	showTotal = (total) => {
		return ` ${total} `;
	}

	click = (type) => {
		this.setState({selectedRowKeys: Object.assign([])});
		this.store.notifyParams.pageIndex = 1;
		this.store.notifyParams.pageSize = 10;
		this.store.notifyParams.type = type;
		this.store.getNotifyPageList();
	}

	// 
	delNotifyList = () => {
		const {selectedRowKeys} = this.state;
		if (selectedRowKeys.length == 0) {
			message.warn("");
			return false;
		}
		this.store.delNotifyList(selectedRowKeys)
		.then((res) => {
			if (res) {
				message.success("");
				this.setState({
					selectedRowKeys: []
				}, () => {
					// 
					this.store.getNotifyPageList();
					emitter.emit("notify", "6");
				});
			}
		});
	}

	// 
	setNotifyListRead = () => {
		const {selectedRowKeys} = this.state;
		if (selectedRowKeys.length == 0) {
			message.warn("");
			return false;
		}
		this.store.setNotifyListRead(selectedRowKeys)
		.then((res) => {
			if (res) {
				message.success("");
				this.setState({
					selectedRowKeys: []
				}, () => {
					// 
					this.store.getNotifyPageList();
					emitter.emit("notify", "6");
				});
			}
		});
	}

	transType = (type) => {
		switch (type) {
			case "comment":
				return "";
				break;
			case "reply":
				return "";
				break;
			default:
				return "";
				break;
		}
	}

	onOpen = (record) => {
		if (record.mainType==null||record.mainType=="") {  // 
			message.warn("");
			if (record.haveRead != 1) {  // 
				this.store.setNotifyRead(record.id).then((res)=>{
					if (res) {
						// 
						this.store.getNotifyPageList();
						emitter.emit("notify", "6");
					}
				});
			}
			return false;
		}
		if (officeArray.includes(record.mainType)) {  //
			this.officeStore.getJournalTypeList().then((res)=>{
				this.officeStore.temList = Object.assign({}, mobx.toJS(this.officeStore.digtalTemData).filter(item=>item.type==record.mainType)[0]);
			});
			this.officeStore.selestedId=record.mainId;
			this.officeStore.getJournalById(record.mainId)
			.then((res) => {
				if (res) {
					this.setState({
						visibleOffic: true,
						selectedID: record.id,
						mainType: record.mainType
					}, () => {
						this.officeStore.detailData = Object.assign({}, res);
					});
				}
			})
			// 
			this.officeStore.getLikeUsersByJournalId();
			// 
			this.officeStore.getCommentsByJournalId();
			this.officeStore.getJournalIndicators();
			this.officeStore.isEdit =false;
		} else {  	//
			this.store.getJournalTypeList().then((res)=>{
				this.store.temList = Object.assign({}, mobx.toJS(this.store.digtalTemData).filter(item=>item.type==record.mainType)[0]);
			});
			this.store.selestedId=record.mainId;
			this.store.getJournalById(record.mainId)
			.then((res) => {
				if (res) {
					this.setState({
						visible: true,
						selectedID: record.id,
						mainType: record.mainType
					}, () => {
						this.store.detailData = Object.assign({}, res);
					});
				}
			})
			// 
			this.store.getLikeUsersByJournalId();
			// 
			this.store.getCommentsByJournalId();
			this.store.getJournalIndicators();
			this.store.isEdit =false;
		}
	}

	onClose = (type) => {
		const {selectedID} = this.state;
		this.setState({
			[type]: false,
		}, ()=>{
			this.store.setNotifyRead(selectedID).then((res)=>{
				if (res) {
					// 
					this.store.getNotifyPageList();
					emitter.emit("notify", "6");
				}
			});
		});
	};

	render() {
		const {selectedRowKeys, visible, visibleOffic} = this.state;
		const {notifyTotalCount, notifyTableData, notifyParams, notifyLoading} = this.store;
		const colorLists = ['#f56a00', '#7265e6', '#ffbf00', '#00a2ae', '#F08080', '#EEE685', '#9400D3', '#D1EEEE', '#90EE90', "#9400D3"]; // 
		const columns = [
			{
				title: '',
				dataIndex: 'name',
				key: 'name',
				render: (text, record) => {
					const {haveRead, msgType, mainTitle, fromUserName, createTime, msg, keyIndex, msgState,canShow} = record;
					const listData = [
						{
							title: keyIndex,
							description: msg
						}
					];
					return <div>
						<List
							itemLayout="vertical"
							size="large"
							dataSource={listData}
							renderItem={item => (
								<List.Item
									key={item.title}
									extra={
										<span style={{
											color: "#666",
											fontSize: '12px'
										}}>{showText(moment(createTime).format("YYYY-MM-DD HH:mm"), moment(createTime).format("HH:mm"))}</span>
									}
								>
									<List.Item.Meta
										avatar={<Avatar
											style={{color: '#FFF',
												    backgroundColor: colorLists[keyIndex]}}>
											{fromUserName.substring(fromUserName.length-2, fromUserName.length)}
										</Avatar>}
										title={<span className="ax_default">
											{haveRead==0?<Badge status={"warning"} text={fromUserName}/>:fromUserName}
										    
											<span style={{color: "#1890ff", cursor: 'pointer'}} onClick={this.onOpen.bind(this, record)}>{mainTitle}</span>
											
											{canShow==1&&msgState==2?<Tag color="gold">{this.transType(msgType)}</Tag>:null}
										</span>
										}
										description={<span style={{
											verticalAlign: 'top',
											lineHeight: 'inherit'
										}}><span
											style={{
												fontSize: '14px',
												color: haveRead=="1"?"#D5D3D6":"#79797B"}}
											className="ax_default">
											{this.transType(msgType)}
											{msgType=="addLike"?null:""}
										</span>
											<div style={{
												display: 'inline-block',
												wordWrap:'break-word',
												whiteSpace: 'pre-wrap',
												fontSize: '14px',
												color: haveRead=="1"?"#D5D3D6":"#79797B"}}>
												{canShow==0?<Tag color="gold">{this.transType(msgType)}</Tag>:item.description}
											</div>
										</span>}
									/>
								</List.Item>
							)}
						/>
					</div>
				}
			}
		];
		const rowSelection = {
			selectedRowKeys,
			onChange: this.onSelectChange
		};
		const {pageIndex, pageSize, type} = mobx.toJS(notifyParams);


		return (<div>
				<Button type={type == 2 ? "primary" : "default"} shape="round"
				        onClick={this.click.bind(this, 2)}></Button>
				<Button type={type == 0 ? "primary" : "default"} shape="round" className="ml10 mr10"
				        onClick={this.click.bind(this, 0)}></Button>
				<Button type={type == 1 ? "primary" : "default"} shape="round"
				        onClick={this.click.bind(this, 1)}></Button>
				<Divider style={{marginBottom: "0"}}/>
				<div className="fr" style={{
					position: "relative",
					top: '39px',
					zIndex: '12'
				}}>
					<Button type="primary" ghost icon="delete" size={"small"} onClick={this.delNotifyList}>
						
					</Button>
					<Button className="ml20" type="primary" ghost icon="eye" size={"small"}
					        onClick={this.setNotifyListRead}>
						
					</Button>
				</div>
				<span className="fl" style={{
					position: "relative",
					top: '42px',
					zIndex: '12',
					left: "48px",
					fontSize: '12px',
					color: '#333'
				}}></span>
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
					<Table
						className="notify_table"
						rowSelection={rowSelection}
						columns={columns}
						dataSource={mobx.toJS(notifyTableData)}
						pagination={false}
						loading={notifyLoading}
						rowKey={record => record.id}
					/>
					<Pagination
						className="mt20 tc"
						current={pageIndex}
						pageSize={pageSize}
						total={notifyTotalCount}
						onShowSizeChange={this.showSizeChange}
						onChange={this.onPageChange}
						showSizeChanger
						showQuickJumper={true}
						showTotal={this.showTotal}
					/>
				</ConfigProvider>
				<DetailModal
					visible={visible}
					onClose={this.onClose.bind(this, "visible")}
					store={this.store}
				/>
				<DetailOfficeModal
					visible={visibleOffic}
					onClose={this.onClose.bind(this, "visibleOffic")}
					store={this.officeStore}
				/>
			</div>
		);
	}
}



